
@import url('https://fonts.googleapis.com/css2?family=Pattaya&family=Roboto&display=swap'); //text body
@font-face {
    font-family: "Market_Deco";
    src: url(../styles/font/Market_Deco.ttf);
}
$font-1:  "Pattaya"; // title
$font-2:  "Roboto"; // body
$font-3:  "Market_Deco"; // H




$color-1: #d3d0ba;
$color-2: #383324;
$color-3: #fade71;
$color-4: #23282d;
$color-5: #fff;
$color-6: #c0a76d;



* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;

}

body {
   
    width: 100vw;
    font-family: $font-2;
    color: $color-5;
    
}

li {
    list-style-type: none;

}

a { 
    text-decoration: none;
}

a:visited {
    color: $color-2; 
}