.navigation { 

    display: flex;
    justify-content: center;
    background-color: $color-6;
    top: 0;
    height: auto;

        a { 
            
            margin-top: 30px;
            margin-right: 20px;
            margin-left: 20px;
            padding-bottom: 10px;
            margin-bottom: 30px;
            color: $color-5;
            position: relative;
        }

        .nav-active { 

            &::after {
                content: '';
                width: 100%;
                height: 10%;
                background-color: $color-2 ;
                position: absolute;
                bottom:1px;
                left:50%;
                transform: translateX(-50%);
                transition: 0.2s;
            }
        }















}