
.batiments{ 


body { width: 100%; 
    height: 100%;
    margin: 0;
    position: fixed;
    background: rgba(25,25,25, 1) }
canvas { width: 100%; height: 100%; outline: none; }
#container { width: 100%; height: 100%; position: relative; }


.spline-watermark {
 position: absolute;
 bottom: 16px;
 right: 16px;
 width: 32px;
 height: 32px;
 z-index: 2;
}

.spline-watermark:hover {
 opacity: 0.8;
}

.spline-watermark img {
 display: block;
 width: 100%;
 height: 100%;
}


}