.popup{




.mapboxgl-popup-content {
    display: block;
    flex-direction: column;
    width:215px;
    height: 260px;
    border-radius: 20px;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border : 5px solid white;


}

        .popup_img img{

            display: block;
            background-size: cover;
            align-items: center;
            position: inherit;
            width:205px;
            border-radius: 15px 15px 0px 0px;
            image-rendering: -webkit-optimize-contrast;
    
        }

        .popup_title {
            background-color: $color-6;
           font-family: $font-3 ;
           font-size: 8px;
           text-align: center;
           padding-top: 5px;
           padding-bottom: 5px;
        }

        .popuplink a {
            font-family: $font-3 ;
            display: flex;
            justify-content: center;
            padding-top: 5px;
            padding-bottom: 5px;
            margin:-4px;
           

        }

        .mapboxgl-popup-close-button{
            color: white;
            margin: 5px;
            position: absolute;
            font-size: 20px;
        }


      
    
}